export default {
	data() {
		return {
			form:'',
			user: '',
			userIcon: false,
			pass: '',
			passIcon: false,
			password: true
		}
	},
	onLoad: function(option) {
		this.form = option.form
	},
	methods: {
		tapUser: function(e) {
			this.user = e.target.value;
			if (e.target.value.length > 0) {
				this.userIcon = true;
			} else {
				this.userIcon = false;
			}
		},
		clearUserIcon: function() {
			this.user = '';
			this.userIcon = false;
		},
		tapPass: function(e) {
			this.pass = e.target.value;
			if (e.target.value.length > 0) {
				this.passIcon = true;
			} else {
				this.passIcon = false;
			}
		},
		clearPassIcon: function() {
			this.pass = '';
			this.passIcon = false;
		},
		tapSee: function(e) {
			this.password = !this.password;
		},
		formSubmit: function(e) {
			if (this.user.length < 1) {
				uni.showToast({
					icon: 'none',
					title: '请输入用户名/手机号',
					duration: 1000
				});
			} else if (this.pass.length < 1) {
				uni.showToast({
					icon: 'none',
					title: '请输入密码',
					duration: 1000
				});
			}
			uni.request({
				url: this.$Url + '/api/login', //仅为示例，并非真实接口地址。
				method: 'POST',
				data: {
					phone: this.user,
					password: this.pass
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					if (res.data.code == 200) {
						uni.setStorageSync('logon_status', 1);
						uni.setStorageSync('token', res.data.token);
						uni.request({
							url: this.$Url + '/api/v1/get/user', //仅为示例，并非真实接口地址。
							method: 'POST',
							data: {
								token: res.data.token,
							},
							header: {
								'content-type': 'application/x-www-form-urlencoded'
							},
							success: (res) => {
								if (res.data.code == 200) {
									uni.setStorageSync('userData', res.data.data);
									uni.setStorageSync('cars_mold', res.data.data.style);
								} else {
									uni.showToast({
										icon: 'none',
										title: '网络不给力，请稍后重试',
										duration: 1000
									});
								}
							}
						});
						uni.showToast({
							title: '登录成功',
							duration: 500,
						});
						if(this.form == 0  || this.form == 1){
							uni.switchTab({
								url: '../tabBar/my/my'
							})
						}else if(this.form == 2){
							uni.switchTab({
								url: '../tabBar/examination/examination'
							})
						}else if(this.form == 3){
							uni.switchTab({
								url: '../tabBar/signUp/signUp'
							})
						}else{
							uni.switchTab({
								url: '../tabBar/my/my'
							})
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.msg,
							duration: 1000
						});
					}
				}
			});
		}
	}
}
